.docs {
	&__title {}
	
		&__search {
			margin-top: 47px;
		}
	
		&__icon {}
	
		&__input {
			&::before {
				content: '';
				display: none ;
			}
		}
	
		&__text {
			margin-top: 46px;
			font-family: 'Styrene A LC Web';
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			color: #000000;

		}



		&__items {
			margin-top: 70px;
		}
			&__item {
				&:not(:first-child) {
					margin-top: 80px;
				}
			}
}
.item-docs {
	&__title {
		font-family: 'Styrene A LC Web';
		font-style: normal;
		font-weight: 700;
		font-size: 24px;
		line-height: 26px;
		color: #F04E00;
	}
	
	&__list {
		margin-top: 50px;
	}
	
	&__item {
		&:not(:first-child) {
			margin-top: 21px;
		}
	}
	
	&__link {
		font-family: 'Styrene A LC Web';
		font-style: normal;
		font-weight: 400;
		font-size: 18px;
		line-height: 26px;
		color: #000000;
		border-bottom: 2px solid #CFEDFF;
		padding: 5px 0;
		transition: all 0.2s cubic-bezier(1, 0.05, 0.43, 1.03) 0s;
		display: inline;
		position: relative;
		box-shadow: inset 0px 0px #CFEDFF;
		&:hover {
			box-shadow: inset 0px -100px 0px #CFEDFF;
		}
		span {
			position: relative;
			z-index: 2;
		}
	}
}
